import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    nickname: "",
    token: "",
    questionnaireObj: {},
  },
  mutations: {
    setUserInfo(state, info) {
      state.nickname = info.nickname;
      state.token = info.token;
    },
    emitQues(state, dataObj) {
      state.questionnaireObj = JSON.parse(JSON.stringify(dataObj));
    },
  },
  getters: {
    Token: (state) => {
      return state.token;
    },
  },
  actions: {},
  modules: {},
});
