import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'


Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Login',
        component: Login
    },
    {
        path: '/edit',
        name: 'EditPagex',
        component: () =>
            import ('../views/EditPagex.vue')
    },
    {
        path: '/homepage',
        name: 'HomePage',
        component: () =>
            import ('../views/HomePage.vue')
    },
    {
        path: '/preview',
        name: 'Preview',
        component: () =>
            import ('../views/Preview.vue')
    },
    {
        path: '/analyse/:id',
        name: 'AnalysisPage',
        component: () =>
            import ('../views/AnalysisPage.vue'),
        children: [{
                path: 'list',
                component: () =>
                    import ('../views/AnalysisList')
            },
            {
                path: 'result',
                component: () =>
                    import ('../views/AnalysisResult')
            }
        ]
    },
    {
        path: '/result',
        name: 'result',
        component: () =>
            import ('../views/result.vue'),
    },

    //temp for code
    {
        path: '/setting',
        name: 'setting',
        component: () =>
            import ('../views/Setting.vue')
    }
]

const router = new VueRouter({
    mode: 'hash',
    routes
})


//  等后端登录接口有了就取消注释
//  除了登录界面，在每一个路由下加入meta: {requiresAuth: true, auth: 0}
router.beforeEach((to, from, next) => {
    // if(to.meta.requiresAuth) {
    //   const auth = window.sessionStorage.getItem('auth')
    //   if(auth == null) {
    //     next({
    //       path:'/'
    //     })
    //   } else {
    //     next()
    //   }
    // } else {
    //   next()
    // }
    next()
})

export function toLogin() {
    router.push('/')
}

export function toError() {
    router.push('/')
}

export default router